import './App.css';
import Header from './components/Header';
import React, { useState } from 'react';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Brown, Black } from './components/theme';
import { DummyJSON } from './components/Dummyjson';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import BuyItem from './components/ItemBuy';
import ItemDetails from './components/ItemDetails';
import Quotes from './components/Quotes';
import Posts from './components/posts';
import Home from './components/Home';

function App() {
  const [BlackTheme, setBlackTheme] = useState([true]);
  return (
    <ThemeProvider theme={BlackTheme ? Black : Brown}>
      <CssBaseline />
      <BrowserRouter>
        <Routes>
          <Route
            path="/"
            element={
              <Header onclick={() => setBlackTheme(!BlackTheme)}></Header>
            }
          >
            <Route path="/" element={<Home />} />
            <Route path="products" element={<DummyJSON url="products" />} />
            <Route path="products/details/:itemId" element={<ItemDetails />} />
            <Route path="products/buy/:itemId" element={<BuyItem />} />
            <Route path="quotes" element={<Quotes />} />
            <Route path="posts" element={<Posts />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
