import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Box, Typography, IconButton } from '@mui/material';
import { IconReact } from './svg.js';
import '@fontsource/roboto/400.css';
import '../App.css';
import { Outlet } from 'react-router-dom';
import FormatQuoteIcon from '@mui/icons-material/FormatQuote';
import HomeIcon from '@mui/icons-material/Home';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import DynamicFeedIcon from '@mui/icons-material/DynamicFeed';

function Header(props) {
  const navigate = useNavigate();
  const NavigateToShop = (event) => {
    navigate('/products/');
  };
  const NavigateToHome = (event) => {
    navigate('/');
  };
  const NavigateToQuotes = (event) => {
    navigate('/quotes');
  };
  const NavigateToPosts = (event) => {
    navigate('/posts');
  };

  return (
    <>
      <Box
        sx={{
          marginBottom: '30px',
          padding: '10px',
          width: '100%',
          height: 'auto',
          bgcolor: 'primary.main',
          color: 'primary.contrastText',
        }}
      >
        <IconButton
          color="inherit"
          onClick={props.onclick}
          aria-label="menu"
          className="rotate"
          disableRipple
          sx={{ float: 'start', mr: 2 }}
        >
          <IconReact />
        </IconButton>
        <IconButton onClick={NavigateToHome}>
          <HomeIcon fontSize="large" color="theme.secondary" />
        </IconButton>
        <IconButton onClick={NavigateToShop}>
          <ShoppingCartIcon fontSize="large" color="theme.secondary" />
        </IconButton>
        <IconButton onClick={NavigateToQuotes}>
          <FormatQuoteIcon fontSize="large" color="theme.secondary" />
        </IconButton>
        <IconButton onClick={NavigateToPosts}>
          <DynamicFeedIcon fontSize="large" color="theme.secondary" />
        </IconButton>
        <Typography sx={{ float: 'right', alignItems: 'center' }} variant="h3">
          {props.children}
        </Typography>
      </Box>
      <Outlet />
    </>
  );
}

export default Header;
