import React, { useRef, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

import axios from 'axios';
import {
  Box,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
  Button,
  CircularProgress,
  Card,
} from '@mui/material';

export function DummyJSON(props) {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(null);

  useEffect(() => {
    setLoading(true);
    axios({
      method: 'GET',
      baseURL: 'https://dummyjson.com',
      url: `/${props.url}`,
    })
      .then(({ data }) => {
        setData(data.products);
      })
      .catch((err) => console.dir(err))
      .finally(() => setLoading(false));
  }, [props.url]);

  const ref = useRef(null);
  const navigateBuy = useNavigate();
  const navigateToBuy = (event) => {
    const id = event.currentTarget.id;
    navigateBuy(`/products/buy/${id}`);
  };
  const navigate = useNavigate();
  const navigateToDetails = (event) => {
    const id = event.currentTarget.id;
    navigate(`/products/details/${id}`);
  };

  return (
    <>
      <Typography sx={{ textAlign: 'center' }} variant="h5">
        Products from Fake Store
      </Typography>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-evenly',
          flexWrap: 'wrap',
          padding: '20px',
          gap: (theme) => theme.spacing(3),
        }}
      >
        {loading && <CircularProgress />}
        {!!data && data.length > 0 ? (
          data.map((product) => {
            return (
              <Card
                elevation={3}
                key={product.id}
                sx={{ maxWidth: 400, maxHeight: 500 }}
              >
                <CardActionArea>
                  <CardMedia
                    ref={ref}
                    id={product.id}
                    onClick={navigateToDetails}
                    component="img"
                    sx={{
                      maxHeight: 300,
                    }}
                    image={product.thumbnail}
                    alt={product.title}
                  />
                  <CardContent>
                    <Typography
                      sx={{
                        mx: '3px',
                        color: 'warning.light',
                      }}
                    >
                      $ {product.price}
                      &emsp; &#9733; {product.rating} &emsp;
                      {product.discountPercentage} % off
                    </Typography>

                    <Typography gutterBottom variant="h5" component="div">
                      {product.title}
                    </Typography>
                    <Typography variant="body2" noWrap color="text.secondary">
                      {product.description}
                    </Typography>
                  </CardContent>
                </CardActionArea>
                <Button
                  ref={ref}
                  id={product.id}
                  onClick={navigateToDetails}
                  sx={{ float: 'right', my: 1.5, mr: 1.5 }}
                  variant="outlined"
                >
                  Details
                </Button>
                <Button
                  ref={ref}
                  id={product.id}
                  onClick={navigateToBuy}
                  sx={{ float: 'right', my: 1.5, mr: 1.5 }}
                  variant="outlined"
                >
                  Buy
                </Button>
              </Card>
            );
          })
        ) : (
          <p>{props.url} isn't available</p>
        )}
      </Box>
    </>
  );
}
