import React, { useState, useEffect } from 'react';

import {
  Box,
  List,
  ListItem,
  ListItemText,
  Paper,
  Typography,
  CircularProgress,
} from '@mui/material';

import axios from 'axios';

function Quotes(props) {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(null);

  useEffect(() => {
    setLoading(true);
    axios({
      method: 'GET',
      baseURL: 'https://dummyjson.com',
      url: '/quotes',
    })
      .then(({ data }) => {
        setData(data.quotes);
      })
      .catch((err) => console.dir(err))
      .finally(() => setLoading(false));
  }, []);
  return (
    <Box
      sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}
    >
      <h2>Quotes</h2>
      {loading && <CircularProgress />}
      {!!data && data.length > 0 ? (
        data.map((quote) => {
          return (
            <List
              key={quote.id}
              sx={{ marginLeft: '10vw', marginRight: '10vw' }}
              component="nav"
              aria-label="mailbox folders"
            >
              <Paper sx={{ width: '100%', maxWidth: '400px' }}>
                <ListItem>
                  <ListItemText primary={quote.quote} />{' '}
                </ListItem>
                <Typography
                  sx={{
                    textAlign: 'end',
                    marginRight: '10px',
                    fontStyle: 'italic',
                  }}
                >
                  - {quote.author}
                </Typography>
              </Paper>
            </List>
          );
        })
      ) : (
        <h4>Quotes Not Available</h4>
      )}
    </Box>
  );
}

export default Quotes;
