import React, { useState, useEffect } from 'react';
import axios from 'axios';
import FavoriteBorderRoundedIcon from '@mui/icons-material/FavoriteBorderRounded';
import {
  CircularProgress,
  List,
  Typography,
  Paper,
  ListItem,
  ListItemText,
  Box,
  Chip,
  Avatar,
  IconButton,
} from '@mui/material';

function Posts() {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(null);

  useEffect(() => {
    setLoading(true);
    axios({
      method: 'GET',
      baseURL: 'https://dummyjson.com',
      url: '/posts',
    })
      .then(({ data }) => {
        setData(data.posts);
      })
      .catch((err) => console.dir(err))
      .finally(() => setLoading(false));
  }, []);

  return (
    <Box
      sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}
    >
      <h2>Posts</h2>
      {loading && <CircularProgress />}
      {!!data && data.length > 0 ? (
        data.map((posts) => {
          return (
            <List
              key={posts.id}
              sx={{ marginLeft: '30px' }}
              component="nav"
              aria-label="mailbox folders"
            >
              <Paper
                elevation={4}
                sx={{ width: '90%', maxWidth: '600px', padding: '10px' }}
              >
                <Box
                  sx={{
                    display: 'flex',
                    justifyContent: 'flex-end',
                    marginX: '30px',
                    alignItems: 'center',
                  }}
                >
                  User : {posts.userId}
                  <IconButton>
                    <Avatar sx={{ width: 28, height: 28 }}>
                      <FavoriteBorderRoundedIcon />
                    </Avatar>
                  </IconButton>
                </Box>
                <hr />
                <ListItem sx={{ flexDirection: 'column' }}>
                  <ListItemText
                    primary={
                      <Typography
                        variant="h5"
                        style={{ letterSpacing: '0.15em', textAlign: 'center' }}
                      >
                        {posts.title}
                      </Typography>
                    }
                  />
                  <ListItemText secondary={posts.body} />
                </ListItem>
                <Box
                  sx={{
                    display: 'flex',
                    justifyContent: 'space-evenly',
                    fontStyle: 'Bold',
                    paddingY: '10px',
                  }}
                >
                  {posts.tags.map((tag) => {
                    return (
                      <Chip
                        key={tag.toString()}
                        variant="outlined"
                        label={tag}
                      />
                    );
                  })}
                </Box>
                <hr />
                <Box
                  sx={{
                    display: 'flex',
                    justifyContent: 'flex-end',
                    marginX: '30px',
                  }}
                >
                  <FavoriteBorderRoundedIcon />
                  &nbsp;
                  {posts.reactions}
                </Box>
              </Paper>
            </List>
          );
        })
      ) : (
        <h4>Quotes Not Available</h4>
      )}
    </Box>
  );
}

export default Posts;
