import { createTheme } from '@mui/material/styles';

export const Brown = createTheme({
  palette: {
    type: 'dark',
    primary: {
      main: '#e78fb3',
      contrastText: '#500423',
    },
    secondary: {
      main: '#9656a1',
      contrastText: '#3b193b',
    },
    background: {
      default: '#271c19',
      paper: '#55423d',
    },
    text: {
      primary: '#fffffe',
      secondary: '#fff3ec',
    },
    divider: '#140d0b',
  },
  typography: {
    fontFamily: 'Short Stack',
    fontSize: 15,
  },
  overrides: {
    MuiSwitch: {
      root: {
        width: 42,
        height: 26,
        padding: 0,
        margin: 8,
      },
      switchBase: {
        padding: 1,
        '&$checked, &$colorPrimary$checked, &$colorSecondary$checked': {
          transform: 'translateX(16px)',
          color: '#fff',
          '& + $track': {
            opacity: 1,
            border: 'none',
          },
        },
      },
      thumb: {
        width: 24,
        height: 24,
      },
      track: {
        borderRadius: 13,
        border: '1px solid #bdbdbd',
        backgroundColor: '#fafafa',
        opacity: 1,
        transition:
          'background-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
      },
    },
  },
});

export const Black = createTheme({
  palette: {
    type: 'dark',
    primary: {
      main: '#7f5af0',
    },
    secondary: {
      main: '#2cb67d',
    },
    background: {
      default: '#242629',
      paper: '#16161a',
    },
    text: {
      primary: '#fffffe',
      secondary: '#94a1b2',
      disabled: '#5e5e5e',
    },
    divider: '#010101',
  },
  typography: {
    fontFamily: 'Grandstander',
    fontSize: 15,
    fontWeightLight: 400,
    fontWeightRegular: 400,
    button: {
      fontFamily: 'Roboto',
    },
  },
  overrides: {
    MuiSwitch: {
      root: {
        width: 42,
        height: 26,
        padding: 0,
        margin: 8,
      },
      switchBase: {
        padding: 1,
        '&$checked, &$colorPrimary$checked, &$colorSecondary$checked': {
          transform: 'translateX(16px)',
          color: '#fff',
          '& + $track': {
            opacity: 1,
            border: 'none',
          },
        },
      },
      thumb: {
        width: 24,
        height: 24,
      },
      track: {
        borderRadius: 13,
        border: '1px solid #bdbdbd',
        backgroundColor: '#fafafa',
        opacity: 1,
        transition:
          'background-color 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
      },
    },
  },
});
